"use client";
import { useForm } from 'react-hook-form';
import { useDispatch, useSelector } from 'react-redux';
import { verifyOTP } from '@/redux/reducers/authReducers';
import { useNavigate, useLocation } from 'react-router-dom';
import { toast } from 'react-toastify';

export default function SignupConfirmation() {
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const location = useLocation();
    const { loading, error } = useSelector((state) => state.auth);
    const email = location.state?.email;

    const {
        register,
        handleSubmit,
        formState: { errors }
    } = useForm();

    const onSubmit = async (data) => {
        if (!email) {
            toast.error('Email not found. Please try again');
            return;
        }

        try {
            const verificationData = {
                email,
                otp: data.code
            };

            const resultAction = await dispatch(verifyOTP(verificationData));
            if (verifyOTP.fulfilled.match(resultAction)) {
                toast.success(resultAction.payload.message);
                navigate('/login');
            }
        } catch (error) {
            toast.error(error?.message || 'Verification failed');
        }
    };

    return (
        <div className="min-h-screen bg-white flex flex-col justify-center py-12 sm:px-6 lg:px-8">
            <div className="sm:mx-auto sm:w-full sm:max-w-md">
                <h1 className="text-center text-3xl font-bold tracking-tight text-gray-900">
                    Check your email for code
                </h1>
                <p className="text-center font-medium pt-2 mt-2">
                    A code has been sent to your email, input it to complete your registration
                </p>
            </div>
            <div className="mt-8 sm:mx-auto sm:w-full sm:max-w-md">
                <div className="bg-white py-8 px-4 sm:rounded-lg sm:px-10">
                    <form className="space-y-6" onSubmit={handleSubmit(onSubmit)}>
                        <div>
                            <input
                                {...register('code', {
                                    required: 'Verification code is required',
                                    pattern: {
                                        value: /^[0-9]{6}$/,
                                        message: 'Please enter a valid 6-digit code'
                                    }
                                })}
                                type="text"
                                placeholder="Enter 6-digit code"
                                className="mt-1 block w-full rounded-md border border-gray-300 px-3 py-2 focus:border-indigo-500 focus:outline-none focus:ring-indigo-500"
                            />
                            {errors.code && (
                                <p className="mt-1 text-sm text-red-600">
                                    {errors.code.message}
                                </p>
                            )}
                        </div>

                        <button
                            type="submit"
                            disabled={loading}
                            className="w-full flex justify-center py-2 px-4 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500 disabled:opacity-50"
                        >
                            {loading ? 'Verifying...' : 'Verify Email'}
                        </button>
                    </form>

                    <div className="mt-6">
                        <p className="text-center text-sm text-gray-600">
                            Didn't receive code?{' '}
                            <button
                                type="button"
                                className="font-medium text-indigo-600 hover:text-indigo-500"
                                onClick={() => {
                                    // Add resend OTP logic here
                                    toast.info('Resend functionality coming soon');
                                }}
                            >
                                Resend
                            </button>
                        </p>
                    </div>
                </div>
            </div>
        </div>
    );
}